@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,500;0,600;1,300;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bubble-wrapper {
    position: absolute;
    display: flex;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    max-height: 100vh;
    z-index: 0; 
    -webkit-filter: url('#goo'); 
            filter: url('#goo');
    overflow-y: hidden;
    overflow-x: hidden;
  }

.color-bar2 {
  width: 400px;
  margin: 4px auto;
  height: 35px;
  background: linear-gradient(270deg, var(--purple), var(--orange));
  border-radius: 8px;
}

.bubble {
  position: absolute;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  background: linear-gradient(250deg, var(--purple), var(--orange));
  -webkit-animation: bubblesAppear 1s linear;
          animation: bubblesAppear 1s linear; 
  -webkit-animation-iteration-count: 1; 
          animation-iteration-count: 1;
  transition-duration: 12s;
  transition-timing-function: ease-in-out;
  box-shadow: 0px 10px 13px -7px #000000;
  -webkit-transform: scale(1) translate(0, 0);
          transform: scale(1) translate(0, 0);
  cursor: pointer;
}

.bubble:hover {
    background: linear-gradient(250deg, white, var(--orange));
}


.bubble-wrapper :nth-child(2) {
  transition-duration: 15s;
  -webkit-animation-duration: 3;
          animation-duration: 3;
}
.bubble-wrapper :nth-child(3) {
  transition-duration: 22s;
  -webkit-animation-duration: 7;
          animation-duration: 7;
}
.bubble-wrapper :nth-child(4) {
  transition-duration: 27s;
  -webkit-animation-duration: 5;
          animation-duration: 5;
}
.bubble-wrapper :nth-child(5) {
  transition-duration: 13s;
  -webkit-animation-duration: 2;
          animation-duration: 2;
}
.bubble-wrapper :nth-child(6) {
  transition-duration: 13s;
  -webkit-animation-duration: 8;
          animation-duration: 8;
}
.bubble-wrapper :nth-child(7) {
  transition-duration: 12s;
}
.bubble-wrapper :nth-child(8) {
  transition-duration: 15s;
}
.bubble-wrapper :nth-child(9) {
  transition-duration: 22s;
}
.bubble-wrapper :nth-child(10) {
  transition-duration: 8s;
  -webkit-animation-duration: 20s;
          animation-duration: 20s;
}



@-webkit-keyframes bubblesAppear {
    0% {
      opacity: 0.9;
    }

    50% {
        /* transform: translate(20px, 0); */
    }

    100% {
        opacity: 1;
    }
}



@keyframes bubblesAppear {
    0% {
      opacity: 0.9;
    }

    50% {
        /* transform: translate(20px, 0); */
    }

    100% {
        opacity: 1;
    }
}
:root {
  --main-color: white;
  --purple: #ae00ff;
  --orange: #d36a2d;
  --blue: rgb(0, 17, 255);
}
.App {
  position: relative;
  text-align: center;
  background: black;
  height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: 'Poppins', sans-serif;;
}

.color-bar {
  height: 45px;
  background: linear-gradient(270deg, #ae00ff, #d36a2d);
  background: linear-gradient(270deg, var(--purple), var(--orange));
  display: flex;
  z-index: 0;
  display: none;
}



.color-bar span {
  margin: auto;
  color: white;
  font-size: 16px;
}

.navbar {
  height: 45px;
  padding: 0px 15% 0px 10%;
  color: white;
  display: flex;
  margin-bottom: 5%;
  z-index: 1;
  justify-content: space-between;
}

.navbar span {
  margin: 0;
  box-sizing: border-box;
  font-size: 28px;
}

.navbar-contact {
  color: white;
  color: var(--main-color);
  padding: 0 1rem;
  margin: auto 0;
  background: none;
  outline: none;
  border: none;
  font-size: 22px;
  border-radius: 20px;
  border: 2px solid white;
  border: 2px solid var(--main-color);
  box-sizing: border-box;
  font-family: inherit;
  font-weight: 400;
  cursor: pointer;
  justify-self: end;
  align-self: end;
  transition: all 0.3s;
}

.navbar-contact:hover {
  background-color: white;
  background-color: var(--main-color);
  color: black;
  transition: all 0.3s;
}

.navbar-logo {
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.main {
  width: 100%;
  margin-top: 3rem
  /* background-color: rgb(29, 29, 29) */;
  display: flex;
  flex-direction: column;
  z-index: 1;
  pointer-events:none;
}

.greeting {
  margin: 5% auto;
  color: white;
  display: flex;
  flex-direction: column;
  /* background: linear-gradient(135deg, rgba(255, 255, 255, 0.078), rgba(255, 255, 255, 0)) ;   
  backdrop-filter: blur(5px); */
  border-radius: 10px;
  padding-bottom: 1rem;
  /* border: 1px solid rgba(255, 255, 255, 0.096) */
  /* box-shadow: 5px 5px 15px 5px #adadad; */
  /* outline: 1px solid white */;
}


.greeting .title {
  font-size: 6.5rem;
  background: -webkit-linear-gradient(-90deg,rgb(255, 255, 255), rgb(207, 207, 207));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  
}

.greeting .details {
  color: rgb(189, 189, 189);
  padding: 0 10%;
}





.svg-blur {
  visibility: hidden;
  height: 0;
  width: 0;
}

.control-panel {
  position: relative;
  width: 30%;
  height: 65px;
  border-radius: 30px;
  /* background-color: rgb(255, 255, 255); */
  margin: auto auto 2rem auto;
  display: flex;
  box-sizing: content-box;
  align-items: center;
  -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);
  outline: 1px solid rgb(255, 255, 255);
  -webkit-animation: panel-appear 1.5s ease-out;
          animation: panel-appear 1.5s ease-out;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}
.control-panel a {
  margin: 0;
  color: white;
  text-decoration: none;
  width: 50%;
  height: 100%;
  display: flex;
  transition-duration: 200ms;
}

.control-panel a:nth-of-type(1) {
  border-radius: 30px 0 0 30px;
}
.control-panel a:last-of-type {
  border-radius: 0 30px 30px 0;
}

.link-active {
  background:white;
  color: white;
}

.link-active div {
  color: black;
}

@-webkit-keyframes panel-appear {
  0% {
    width: 5%;
    opacity: 0;
  }

  35% {
    width: 5%;
    opacity: 0.6;
  }

  100%{
    width: 30%;
    opacity: 1;
  }
}

@keyframes panel-appear {
  0% {
    width: 5%;
    opacity: 0;
  }

  35% {
    width: 5%;
    opacity: 0.6;
  }

  100%{
    width: 30%;
    opacity: 1;
  }
}

.control-previous, .control-next {
  margin: auto;
  font-size: 2rem;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-animation: control-text-appear 1.5s linear;
          animation: control-text-appear 1.5s linear;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}


@-webkit-keyframes control-text-appear {
  0% {
    opacity: 0;
  }

  75% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}


@keyframes control-text-appear {
  0% {
    opacity: 0;
  }

  75% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}


@media screen and (max-width: 500px) {
  .App {
    justify-content: space-between;
  }

  .main {
    margin-bottom: 3rem;
  }

  .navbar {
    padding-right: 0;
  }

  .navbar-contact {
    height: 46px;
    font-size: 1.2rem;
    padding: 0 0.5rem;
    border-radius: 20px;
  }

  .control-panel{
    width: 100%;
  }

  .greeting .title {
    font-size: 4rem;
  }

  .greeting .details {
    font-size: 1rem;
  }

  @-webkit-keyframes panel-appear {
    0% {
      width: 5%;
      opacity: 0;
    }
  
    35% {
      width: 5%;
      opacity: 0.6;
    }
  
    100%{
      width: 100%;
      opacity: 1;
    }
  }

  @keyframes panel-appear {
    0% {
      width: 5%;
      opacity: 0;
    }
  
    35% {
      width: 5%;
      opacity: 0.6;
    }
  
    100%{
      width: 100%;
      opacity: 1;
    }
  }
}

.project-list {
    display: flex;
    width: 100%;
    margin: auto;
    justify-content: space-between;
    padding: 0 5%;
    box-sizing: border-box;
    z-index: 1;
    /* pointer-events: none; */
}
.project {
    pointer-events: all;
    display: flex;
    flex-direction: column;
    width: 300px;
    height: 400px;
    border-radius: 20px;
    background-color: rgba(255, 255, 255, 0.445);
    -webkit-backdrop-filter: blur(8px);
            backdrop-filter: blur(8px);
    cursor: pointer;
    transition-duration: 500ms;
    -webkit-animation: appear 300ms ease-out;
            animation: appear 300ms ease-out;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}
.project header {
    height: 50%;
    border-radius: 20px 20px 0 0;
}
.project-details {
    height: 50%;
    background-color: white;
    border-radius: 0 0 20px 20px;
    padding: 0 5%;
    box-sizing: border-box;
}

.project-title {
    font-size: 3rem;
    font-weight: 600;
    margin: 0;
}

.project:hover {
    background-color: rgba(48, 48, 48, 0.671);
}

@-webkit-keyframes appear {
    0% {
        -webkit-transform: scale(0.5);
                transform: scale(0.5);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes appear {
    0% {
        -webkit-transform: scale(0.5);
                transform: scale(0.5);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
